* {
    box-sizing: border-box;
  }
  .profileScreen {
    height: 100vh;
    color: #fff;
    background-color: #111;
  }
  
  .profileScreen__body {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 40px;
  }
  
  .profileScreen__info {
    display: flex;
  }
  
  .profileScreen__info > img {
    height: 100px;
  }
  
  .profileScreen__body > h1 {
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
  }
  
  .profileScreen__details {
    color: white;
    margin-left: 25px;
    flex: 1;
  }
  
  .profileScreen__details > h2 {
    background-color: gray;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
  }
  
  .profileScreen__signout {
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: white;
    background-color: #e50914;
    cursor: pointer;
    border: none;
  }
  
  .profileScreen__plans {
    margin-top: 20px;
  }
  
  .profileScreen__info > h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;
  }
  
  @media (max-width: 450px) {
    .profileScreen {
      position: relative;
    }
    .profileScreen__body {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      padding: 1rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      text-align: left;
    }
    .profileScreen__body > h1 {
      font-size: 2.5rem;
    }
  }