.nav {
   position: fixed;
   top: 0;
   padding: 10px;
   width: 100%;
   height: 30px;
   z-index: 1;
   
   transition-timing-function: ease-in;
   transition: all 0.5s;
}

.nav_contents {
    display: flex;
    justify-content: space-between;
}

.nav_black {
    background-color: #111;
}

.nav_logo {
    position: fixed;
    top: 0;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav_avatar {
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 30px;
}